=LarsBold
    font-family: 'Larsseit'
    font-style: normal
    font-weight: bold

=LarsNormal
    font-family: 'Larsseit'
    font-style: normal
    font-weight: normal

=NoeBold
    font-family: 'Noe Display'
    font-style: normal
    font-weight: bold

*
    margin: 0
    padding: 0
    box-sizing: border-box
a
    text-decoration: none

html
    overflow-x: hidden !important

body
    overflow-x: hidden !important
    background: #011627

                                                            /*Header*/

.wrapper
    max-width: 1460px
    margin: 0 auto
    padding: 0 20px

.wrapper__footer
    max-width: 1065px
    margin: 0 auto
    padding: 0 20px


.Header
    background: url(../Photos/Header/Bg__header.png) no-repeat center
    width: 100%
    max-width: 1920px
    height: 100%
    margin-bottom: 100px
    background-size: cover
    &::after
        content: ''
        display: block
        width: 100%
        height: 300px
        background: linear-gradient(180deg, rgba(1, 22, 39, 0) 0%, #011627 76.64%, #011627 100%)

.nav__menu
    padding-top: 75px
    display: flex
    justify-content: space-between
    align-items: center

.menu
    img
        margin-bottom: -7px
        &:hover
            cursor: pointer
    a
        button
            @include LarsBold
            background: #00C9E0
            box-shadow: 5px 15px 40px rgba(0, 201, 224, 0.2)
            border-radius: 48px
            padding: 20px 58px
            border: 0
            color: #fff
            margin-left: 32px
            z-index: 5
            transition: .2s ease all
            &:hover
                cursor: pointer
                background: #11aec0
            &::after
                content: ''
                float: right
                z-index: 100 
                margin-right: -70px
                margin-top: 7px
                width: 30px
                height: 2px
                background: #FFFFFF
                border-radius: 16px

.header__Sec
    h1
        @include NoeBold
        text-align: center
        margin: 156px 0 14px
        font-size: 120px
        line-height: 153px
        letter-spacing: -0.02em
        color: #FFFFFF
    h3
        @include LarsBold
        font-size: 18px
        text-align: center
        line-height: 24px
        letter-spacing: 0.1em
        text-transform: uppercase
        color: #FFFFFF
    p
        @include LarsNormal
        font-size: 17px
        width: 643px
        margin: 50px auto 0
        line-height: 150%
        text-align: center
        color: #FFFFFF

.scroll__networks
    display: flex
    justify-content: space-between
    margin-top: 192px
    .scroll
        display: flex
        align-items: center
        transform: rotate(-90deg)
        .line__scroll
            background: linear-gradient(180deg, #09F4F4 0%, #00C9E0 100%)
            border-radius: 13px
            margin-right: 15px
            width: 80px
            height: 2px
        p
            margin: 0
            width: auto
            font-size: 15px
            line-height: 190%
            letter-spacing: 0.05em
            color: #FFFFFF
            opacity: 0.9

    .networks
        display: flex
        flex-direction: column
        justify-content: center
        a
            margin-bottom: 22px
            &:last-child
                margin-bottom: 0



                                                        /*BG Lights*/

.Bg__2__sec
    position: absolute
    top: 1160px
    height: 4413px
    width: 100%
    z-index: -100
    left: 0
    background: url(../Photos/Lights/BG__LIGHTS.png) no-repeat 

                                                            /*Trip*/


.Trip
    margin-bottom: 300px


.title__trip
    display: flex
    align-items: center
    h1
        @include NoeBold
        margin-right: 35px
        text-align: left
        font-size: 72px
        line-height: 110%
        text-align: center
        letter-spacing: -0.02em
        color: #FFFFFF
    p
        @include LarsNormal
        margin-top: 12px
        width: 360px
        font-size: 16px
        line-height: 150%
        letter-spacing: -0.02em
        color: #FFFFFF


.lists__trip
    margin-top: 80px
    display: flex
    justify-content: space-around
    margin-bottom: 150px
    a
        @include LarsBold
        outline: 0
        outline-offset: 0
        width: 100%
        font-size: 16px
        line-height: 110%
        text-align: center
        letter-spacing: -0.02em
        color: #FFFFFF
        opacity: 0.5
        z-index: 1000
        transition: .4s ease all
        .line__trip__li
            width: 100%
            height: 2px
            margin-top: 30px
            background: #41EAD4
            opacity: 0.3
            border-radius: 13px
            transition: .4s ease all

.active__lists__trip
    color: #41EAD4 !important
    opacity: 1 !important

    .line__trip__li
        height: 4px !important
        opacity: 1 !important



                
.active__li
    &:first-child::after
        content: ''
        display: block
        width: 100%
        height: 4px
        margin-top: 33px
        background: #41EAD4
        border-radius: 13px

