.block1__swiper
    margin: -238px 130px 0 0
    .swiper 
        width: 502px
        height: 607px
    &::before
        content: ''
        display: block
        position: relative
        top: 660px
        width: 238px
        height: 230px
        left: -57px
        background: url(../Photos/Swiper__section/Dots.png) no-repeat
    &::after
        content: ''
        display: block
        position: relative
        bottom: 380px 
        width: 210px
        height: 120px
        left: -97px
        z-index: 1000
        background: url(../Photos/Swiper__section/01.png) no-repeat

.swiper-button-next, .swiper-button-prev
    position: absolute !important
    top: 93% !important

    
.swiper-button-prev
    background: url(../Photos/Swiper__section/prev__arrow.png) no-repeat center
    width: 67px !important
    height: 65px !important
    background-position: center
    left: 73% !important
    transition: .2s ease all
    &:hover
        opacity: .9
    

.swiper-button-prev::after 
    display: none

.swiper-button-next
    background: url(../Photos/Swiper__section/next__arrow.png) no-repeat center
    width: 67px !important
    height: 65px !important
    background-position: center
    right: -1px !important
    transition: .2s ease all
    &:hover
        opacity: .9

.swiper-button-next::after 
    display: none
        
